import React, {  useEffect, useState } from 'react';
import { getSecure } from '../redux/actions/app'
import Title from '../component/Title'
import Preorder from '../component/Preorders'
import Error from '../component/Error'

const Main = React.memo(
    (props) =>{
        let [preorders, setPreorders] = useState([]);
        let del = async(preitem) => {
            let data = await getSecure({name: 'УдалитьПредзаказ', data: {preitem: preitem}})
            if(data==='ok'){
                data = await getSecure({name: 'ПредзаказПользователь'})
                if(data!=undefined)
                    await setPreorders(data)
                else setPreorders([])
            }
        };
        useEffect(async ()=>{
            let data = await getSecure({name: 'ПредзаказПользователь'})
            if(data!=undefined)
                await setPreorders(data)
            if(window.rerun!=undefined)
                await window.rerun()
        },[]);
        return (
            <>
            <Title title="Мои предзаказы"/>
            {preorders.length>3?
                preorders.map((element, idx) => {
                    if(idx!==0&&(idx+1)%4===0) {
                        return <Preorder del={del} type={2} history={props.history} items={[preorders[idx-3], preorders[idx-2], preorders[idx-1], preorders[idx]]}/>
                    }
                    else if(idx+1===preorders.length) {
                        if((idx+1)%3===0)
                            return <Preorder del={del} type={2} history={props.history} items={[preorders[idx-2], preorders[idx-1], preorders[idx]]}/>
                        else if((idx+1)%2===0)
                            return <Preorder del={del} type={2} history={props.history} items={[preorders[idx-1], preorders[idx]]}/>
                        else
                            return <Preorder del={del} type={2} history={props.history} items={[preorders[idx]]}/>
                    }
                })
                :preorders.length>1?
                    <Preorder type={2} del={del} history={props.history} items={[preorders[0], preorders[1], preorders[2]]}/>
                    :preorders.length>1?
                        <Preorder type={2} del={del} history={props.history} items={[preorders[0], preorders[1]]}/>
                        :preorders.length>0?
                            <Preorder type={2} del={del} history={props.history} items={[preorders[0]]}/>
                            :<Error/>
            }
            </>
        );
    })

export default Main;
